import React from 'react';
import '../assets/styles/Form.scss';

const Form = () => (
    <div className="container mt-4 mb-4">
        <div className="row">
            <div className="col text-center mb-2">
                <h2>Consultas!</h2>
            </div>
        </div>
        <div className="row">
            <div className="col col-md-8 offset-md-2 col-lg-8 offset-lg-2 pt-2">
                <form>
                    <div className="form-row mt-2 mb-2">
                        <div className="col-12 col-md-6">
                            <input type="text" className="form-control" placeholder="Nombre" />
                        </div>
                        <div className="col-12 col-md-6">
                            <input type="text" className="form-control" placeholder="Apellido" />
                        </div>
                    </div>
                    <div className="form-row mt-2 mb-2">
                        <div className="col mt-2 mb-2">
                            <input type="text" className="form-control" placeholder="Email" />
                        </div>    
                    </div>

                    <div className="form-row">
                        <div class="form-goup col">
                            <textarea name='text' class="form-control form-control-lg" placeholder="Envíame un mensaje..." required></textarea>
                            <div class="valid-feedback">
                            Correcto!!
                            </div>
                            <div class="invalid-feedback">
                            Debes de escribir sobre qué quieres hablar.
                            </div>
                            <small class="form-text text-muted">
                            Recuerda completar ciudad.
                            </small>
                        </div>
                    </div>

                    <div class="form-row">
                        <div class="col">
                            <button type='submit' className='btn btn-success btn-block' >Enviar</button>
                        </div>
                    </div>

                </form>
            </div>
        </div>
    </div>
);

export default Form;